footer {
  background-color: black;
  color: rgb(230, 225, 225);
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  padding: 20px 0;
}
.fotSection {
  width: 300px;
  margin: 10px;
}
.fotSection h5 {
  color: var(--main-color);
  font-size: 1rem;
}
.fotSection a {
  text-decoration: none;
  color: rgb(230, 225, 225);
  width: 20px;
}
/* Copy */
.copy {
  background-color: rgb(77, 72, 72);
  color: white;
  text-align: center;
  padding: 2px;
}
.copy h3 {
  font-size: 0.8rem;
  font-weight: lighter;
}
/*  */
@media only screen and (max-width: 950px) {
  footer {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
