.donationsection {
  background-color: var(--main-color);
  min-height: 50vh;
  padding: 5% var(--main-padding);
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Nunito Sans", sans-serif;
}
.donationbanl {
  background-color: white;
  border-radius: 15px;
  width: 100%;
  padding: 7% 7%;
}
.donationsection h2 {
  color: white;
  font-size: 4rem;
  font-family: "Sofia", cursive;
}
.donationsection h3 {
  text-align: center;
  color: white;
  font-size: 2.5rem;
  margin-bottom: 5%;
  font-family: "Prompt", sans-serif;
}
.donationbanl p {
  font-size: clamp(1rem, 1.8rem, 1.9rem);
}
