/* font-family: 'Nunito Sans', sans-serif;
font-family: 'Prompt', sans-serif;
font-family: 'Sofia', cursive; */
.act {
  padding: 4% 0;
}
.activities {
  padding: 3% var(--main-padding);
  padding-top: 0%;
  font-family: "Prompt", sans-serif;
}
.activities h3 {
  text-align: center;
  font-size: 3rem;
}
.activitieItems {
  padding: 0% var(--main-padding);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
  gap: 2rem;
}
.aItem {
  padding: 5%;
  background-color: #e06069bc;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  transition: all 0.3s;
}
.aItem:hover {
  background-color: #eb7d84bc;
}
.aItem h4 {
  font-size: 1.5rem;
  font-family: "Prompt", sans-serif;
  margin-bottom: 10px;
}
.aItem p {
  font-family: "Prompt", sans-serif;
}
.aItem img {
  position: relative;
  margin-top: 15px;
  width: 100%;
  border-radius: 20px;
}
.actBtn {
  margin: 3%;
}
.actBtn h5 {
  text-align: center;
  font-size: 1.5rem;
  font-family: "Prompt", sans-serif;
  border: 2px solid var(--main-color);
  width: 100px;
  margin: auto;
  transition: all 0.3s;
  color: var(--main-color);
}
.actBtn h5:hover {
  background-color: var(--main-color);
  color: white;
}
@media only screen and (max-width: 580px) {
  .activitieItems {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}
